import React, { useState } from "react";
import { Layout, Steps, Typography, Row, Col } from "antd";
import { AlertOutlined, LaptopOutlined, RocketOutlined, FireOutlined, SyncOutlined } from '@ant-design/icons';
import { Born } from './carrier-detail/Born';
import { School } from './carrier-detail/School';
import { StartEngineer } from './carrier-detail/StartEngineer';
import { HometownTax } from './carrier-detail/HometownTax';
import { Now } from './carrier-detail/Now';
import useMediaQuery from 'use-media-antd-query';

const { Content } = Layout;
const { Title } = Typography;

const ITEMS = [
  {
    title: '1995',
    description: '',
    icon: <AlertOutlined />,
  },
  {
    title: '2018',
    description: '',
    icon: <LaptopOutlined />
  },
  {
    title: '2018',
    description: '',
    icon: <RocketOutlined />
  },
  {
    title: '2020',
    description: '',
    icon: <SyncOutlined />
  },
  {
    title: '2023',
    description: '',
    icon: <FireOutlined />
  },
]

export const CarrierSection: React.FC = () => {
  const [current, setCurrent] = useState(0);
  // "xs" | "sm" | "md" | "lg" | "xl" | "xxl"
  const colSize = useMediaQuery();
  const onChange = (value: number) => {
    setCurrent(value);
  };

  const renderStepContent = () => {
    switch (current) {
      case 0:
        return <Born />;
      case 1:
        return <School />;
      case 2:
        return <StartEngineer />;
      case 3:
        return <HometownTax />;
      case 4:
        return <Now />;
      default:
        return null;
    }
  };

  return (
    <Layout id="career">
      <Title style={{ width: '90%', margin: '24px auto' }}>Carrier</Title>
      <Content>
        <Row justify='center'>
          <Col lg={8} xl={8} xs={20} offset={colSize === 'lg' || colSize === 'xl' ? 2 : 0}>
            <Steps
              onChange={onChange}
              responsive
              current={current}
              direction="vertical"
              items={ITEMS}
            />
          </Col>
          <Col lg={8} xl={8} xs={20} offset={colSize === 'lg' || colSize === 'xl' ? 2 : 0}>
            {renderStepContent()}
          </Col>
        </Row>
      </Content>
    </Layout>
  )
}