import { useForm, Controller } from 'react-hook-form';
import { Button, Form, Input, Typography, Row, Col, Layout, message } from 'antd';
import { SendOutlined } from '@ant-design/icons';
import useMediaQuery from 'use-media-antd-query';
import { useState } from 'react';

const { TextArea } = Input;
const { Title } = Typography;

interface FormValue {
  name: string;
  email: string;
  phone: number;
  company: string | undefined;
  message: string;
}

export const ContactComponent = () => {
  const colSize = useMediaQuery();
  const [resetForm, setResetForm] = useState(false);
  const [messageApi, contextHolder] = message.useMessage();
  const {
    control,
    handleSubmit,
  } = useForm<FormValue>();

  const successMessage = () => {
    messageApi.open({
      type: 'success',
      content: '',
    });
  };

  const errorMessage = () => {
    messageApi.open({
      type: 'error',
      content: '',
    });
  };

  const buildSlackMessage = (data: FormValue) => {
    const company = data.company ?? '';
    const email = data.email
    const message = data.message
    const name = data.name
    const phone = data.phone

    return `
: ${company}\n
: ${email}\n
: ${message}\n
: ${name}\n
: ${phone}
    `;
  }

  const postMessage = async (data: FormValue) => {
    const message = buildSlackMessage(data);
    try {
      const response = await fetch('https://new-express-project-navy.vercel.app/sendToSlack', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ data: message })
      });

      if (response.ok) {
        successMessage();
      } else {
        errorMessage();
        console.error('Slack');
      }
    } catch (error) {
      console.error('Slack:', error);
      errorMessage();
    }
  }

  const onSubmit = (data: FormValue) => {
    postMessage(data);
    setResetForm(true);
  };

  return (
    <Layout id='contact'>
      {contextHolder}
      <Form
        onFinish={handleSubmit(onSubmit)}
        layout="vertical"
        wrapperCol={{ span: 12 }}
        style={{ padding: '50px 0' }}
      >
        <Row
          justify={{ xs: 'start', lg: 'center', xl: 'center' }}
          align='middle'
        >
          <Col xs={20} lg={18} xl={18} offset={colSize === 'lg' || colSize === 'xl' ? 10 : 2}>
            <Title>Contact</Title>
            {/*  */}
            <Controller
              control={control}
              name='name'
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                  help={error?.message}
                  rules={[{ required: true, message: '' }]}
                >
                  <Input
                    {...field}
                  />
                </Form.Item>
              )}
            />

            {/*  */}
            <Controller
              control={control}
              name='company'
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                >
                  <Input
                    {...field}
                  />
                </Form.Item>
              )}
            />

            {/*  */}
            <Controller
              control={control}
              name='email'
              rules={{ required: true }}
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                  help={error?.message}
                  rules={[{ required: true, message: '' }]}
                >
                  <Input
                    {...field}
                  />
                </Form.Item>
              )}
            />

            {/*  */}
            <Controller
              control={control}
              name='phone'
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                >
                  <Input
                    {...field}
                  />
                </Form.Item>
              )}
            />

            {/*  */}
            <Controller
              control={control}
              name='message'
              render={({ field, fieldState: { error } }) => (
                <Form.Item
                  label=''
                  name={field.name}
                  validateStatus={error ? 'error' : 'success'}
                  help={error?.message}
                  rules={[{ required: true, message: '' }]}
                >
                  <TextArea
                    {...field}
                  />
                </Form.Item>
              )}
            />

            <Row justify="start">
              <Col xl={20} lg={20} xs={12} offset={colSize === 'lg' || colSize === 'xl' ? 4 : 4}>
                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                    shape="round"
                    size='large'
                    icon={<SendOutlined />}
                  >
                    
                  </Button>
                </Form.Item>
              </Col>
            </Row>

          </Col>
        </Row>
      </Form>
    </Layout>
  )
}
